"use client";

import { useParams } from "next/navigation";
import { mockClients, mockTransactions } from "@/data/mockData";
import WalletHeader from "@/components/wallet/WalletHeader";

export default function ClientDetailPage() {
  const params = useParams();
  const clientId = params.id as string;
  
  const client = mockClients.find(c => c.id === clientId);
  const clientTransactions = mockTransactions.filter(t => 
    t.description.toLowerCase().includes(client?.name.toLowerCase() || '')
  );

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('pt-BR');
  };

  if (!client) {
    return (
      <main className="min-h-screen bg-black">
        <WalletHeader />
        <div className="max-w-6xl mx-auto px-4 py-6">
          <p className="text-white">Cliente não encontrado</p>
        </div>
      </main>
    );
  }

  const totalReceived = clientTransactions
    .filter(t => t.type === 'income')
    .reduce((sum, t) => sum + t.amount, 0);

  const totalPending = clientTransactions
    .filter(t => t.type === 'income' && t.status === 'pending')
    .reduce((sum, t) => sum + t.amount, 0);

  return (
    <main className="min-h-screen bg-black">
      <WalletHeader />
      
      <div className="max-w-6xl mx-auto px-4 py-6">
        <div className="mb-6">
          <h1 className="text-3xl font-bold text-white mb-2">{client.name}</h1>
          <p className="text-gray-400">Processo: {client.caseNumber}</p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-gray-900 rounded-lg p-6 border border-gray-800">
            <h3 className="text-sm font-medium text-gray-400 mb-2">Total Recebido</h3>
            <p className="text-2xl font-bold text-green-400">{formatCurrency(totalReceived)}</p>
          </div>
          
          <div className="bg-gray-900 rounded-lg p-6 border border-gray-800">
            <h3 className="text-sm font-medium text-gray-400 mb-2">Pendente</h3>
            <p className="text-2xl font-bold text-yellow-400">{formatCurrency(totalPending)}</p>
          </div>
          
          <div className="bg-gray-900 rounded-lg p-6 border border-gray-800">
            <h3 className="text-sm font-medium text-gray-400 mb-2">Email</h3>
            <p className="text-lg text-white">{client.email}</p>
          </div>
        </div>

        <div>
          <h2 className="text-2xl font-semibold text-white mb-4">Transações do Cliente</h2>
          <div className="space-y-4">
            {clientTransactions.map((transaction) => (
              <div
                key={transaction.id}
                className="bg-gray-900 rounded-lg p-4 border border-gray-800"
              >
                <div className="flex justify-between items-start">
                  <div>
                    <h4 className="text-white font-medium">{transaction.description}</h4>
                    <p className="text-sm text-gray-400">{formatDate(transaction.date)}</p>
                  </div>
                  <div className="text-right">
                    <p className={`text-lg font-semibold ${transaction.amount > 0 ? 'text-green-400' : 'text-red-400'}`}>
                      {transaction.amount > 0 ? '+' : ''}{formatCurrency(transaction.amount)}
                    </p>
                    <span className={`text-xs px-2 py-1 rounded ${
                      transaction.status === 'completed' ? 'bg-green-800 text-green-200' :
                      transaction.status === 'pending' ? 'bg-yellow-800 text-yellow-200' :
                      'bg-red-800 text-red-200'
                    }`}>
                      {transaction.status === 'completed' ? 'Concluído' :
                       transaction.status === 'pending' ? 'Pendente' : 'Falhou'}
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </main>
  );
}
